@tailwind base;
@tailwind components;
@tailwind utilities;

#app {
    display: grid;
    grid-template-columns: repeat(15, 1fr);
    grid-template-rows: repeat(10,1fr);
}

#nav {
    grid-column: 1/16;
    grid-row: 1/2;
    background-color: bisque;
}

#estadistica {
    background-color: lightcoral;
    grid-column: 1/16;
    grid-row: 2/8;
}
#calendario {
    background-color: lightgoldenrodyellow;
    grid-column: 1/16;
    grid-row: 2/8;
}
#ingresar {
    background-color: lightskyblue;
    grid-column: 1/16;
    grid-row: 2/8;
}
#lista {
    background-color: lightseagreen;
    grid-column: 1/16;
    grid-row: 2/8;
}
#btn-container {
    background-color: black;
    grid-column: 1/16;
    grid-row: 8/10;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.btn{
    width: 12vw;
    height: 12vh;
    border-radius: 22px;
}

#footer {
    background-color: lightsalmon;
    grid-column: 1/16;
    grid-row: 10/11;
}
